import React from "react"
import { Container } from "../../style"
import { graphql, useStaticQuery } from "gatsby"
import * as SC from "./Manifest"

const Manifest = () => {
  const data = useStaticQuery(graphql`
    query authorImage {
      placeholderImage: file(relativePath: { eq: "author.jpg" }) {
        childImageSharp {
          fluid(maxWidth: 350, quality: 90) {
            ...GatsbyImageSharpFluid
          }
        }
      }
    }
  `)

  const imageData = data.placeholderImage.childImageSharp.fluid
  return (
    <SC.Section id="manifest">
      <Container>
        <SC.Wrapper>
          <SC.InfoTitle>  ,   </SC.InfoTitle>
          <SC.SecondaryWrapper>
            <SC.Author>
              <SC.AuthorImage
                fluid={imageData}
                style={{ backgroundSize: "cover" }}
              />
              <SC.AuthorInfoWrapper>
                <SC.AuthorName> </SC.AuthorName>
                <SC.AuthorText>
                   , <br />
                   
                </SC.AuthorText>
              </SC.AuthorInfoWrapper>
            </SC.Author>
            <SC.Info>
              <SC.InfoText>
                 ,        ,
                      - . ,
                 ,   :
              </SC.InfoText>
              <SC.InfoRules>
                <ManifestRule
                  title={` `}
                  text={`      ,    `}
                  number={1}
                />
                <ManifestRule
                  title={` `}
                  text={`   ,    (  )`}
                  number={2}
                />
                <ManifestRule
                  title={`  `}
                  text={`        .      `}
                  number={3}
                />
              </SC.InfoRules>
            </SC.Info>
          </SC.SecondaryWrapper>
        </SC.Wrapper>
      </Container>
    </SC.Section>
  )
}

export default Manifest

const ManifestRule = ({ title, text, number }) => {
  return (
    <SC.Rule>
      <SC.RuleTitle>{title}</SC.RuleTitle>
      {text}
      <SC.RuleNumber>{number}</SC.RuleNumber>
    </SC.Rule>
  )
}
